
.projects {
  padding: 0px 6% 0 6%;
  text-align: center;
  margin-top: 0px;
}
.project-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-top: 0px;
}
.project {
  max-height: 540px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  max-width: 280px;
  min-width: 280px;
  margin: 2%;
  padding-top: 80px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
h1 {
  font-weight: 600;
  max-width: 100vw;
  white-space: nowrap;
  text-align: center;
  padding-top: 20px;
  margin-top: 0px;
  color:purple;
}
.project h4 {
  font-weight: 600;
  font-size: 20px;
}
.project img {
  width: 350px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0.8;
}
.project:hover img {
  opacity: 1;
}
.project:hover .project-details {
  -webkit-transform: translateY(-30px);
          -ms-transform: translateY(-30px);
      transform: translateY(-30px);
}

.img-container {
  height: 210px;
  overflow: hidden;
  width: 280px;
}

.project-details {
  color: white;
  background-color: #333;
  border-radius: 0 0 10px 10px;
  padding: 10px;
  height: 180px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-transform: translateY(-100px);
          -ms-transform: translateY(-100px);
      transform: translateY(-100px);
  width: 280px;
}



