
body{
  padding:0;
  margin:0;
  font-family: Helvetica;
}
.navbar-inverse{
  margin-bottom: 0px;
  border-radius: 0px;
}
.link{
  color: #ffbe00;
  text-decoration: none;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 13px;
}
.link:hover{
  color:white;
  text-decoration: none;
}
a:focus, a:hover{
  color: #fff;
  text-decoration: none;
}



