
.hey{
  width:100%;
  height:650px;
  background: papayawhip;
}
.wrapper{
  position:relative;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  height:auto;
  width:67%;
  background: rgba(0,0,0,.5);
  text-align: center;
  border-radius: 5px;
  padding: 20px;
}
.avatar-img{
  height: 90px;
  border-radius: 50%;
  padding-top: .5em;
  -webkit-animation:slideInDown 1s ease-in-out;
          animation:slideInDown 1s ease-in-out;
}
.homepage-text{
  width: 85%;
  margin: auto;
  border-radius: 2px;
  text-align: center;
}
@media (max-width:700px) {
  .homepage-text{
    width: 100%;
    margin:0px ;
  }
  .wrapper{
    width:100%;
    height: 100%;
  }
  .homepage-text p{
    margin: 10px;
    color: #111;
  }
}
.homepage-text h2{
  font-size: 23px;
  color:papayawhip;
}
.homepage-text h2 span{
  -webkit-animation: pulse 1s ease-in-out 2s 2;
          animation: pulse 1s ease-in-out 2s 2;
}
.homepage-text hr{
  border-top: 2px Dotted #777;
  width:67%;
}
.homepage-text h6{
  color: #000;
  padding: .5em;
}
.homepage-h6-1{
  -webkit-animation: zoomIn 1s ease-in-out .3s;
          animation: zoomIn 1s ease-in-out .3s;
}
.homepage-h6-2{
  -webkit-animation: zoomIn 1.1s ease-in-out .6s;
          animation: zoomIn 1.1s ease-in-out .6s;
}
.homepage-h6-3{
  -webkit-animation: zoomIn 1.3s ease-in-out .9s;
          animation: zoomIn 1.3s ease-in-out .9s;
}
.homepage-h6-4{
  -webkit-animation: zoomIn 1.4s ease-in-out 1.2s;
          animation: zoomIn 1.4s ease-in-out 1.2s;
}
.homepage-h6-5{
  -webkit-animation: zoomIn 1.5s ease-in-out 1.4s;
          animation: zoomIn 1.5s ease-in-out 1.4s;
}
.homepage-h6-6{
  -webkit-animation: zoomIn 1.6s ease-in-out 1.6s;
          animation: zoomIn 1.6s ease-in-out 1.6s;
}
.homepage-text p{
  font-size: 12px;
  color: papayawhip;
  text-align: justify;
}

/*this section is for contact page */
.contact-grid h2{
  font-size: 30px;
  color:papayawhip;
}
.contact-grid hr{
  border-top: 2px Dotted orange;
  margin:auto;
  width:67%;
}
.contact-grid p{
  padding-top: 5%;
  text-align:center;
  color:papayawhip;
}



